.primary {
    color: blue;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-align: center;
    height: 50%;
    background-color: bisque;
    width: 450px;
    position: relative;
    padding: 50px 50px;
    display: table;
    margin: auto;
    border-radius: 25px;
}

.next {
    border: none;
    font-size: 18px;
    text-align: center;
    color: blue;
    background-color: bisque;
    padding: 15px 32px;
    cursor: pointer;
    margin-top: 20px;
    float: right;
}
.previous {
    border: none;
    font-size: 18px;
    text-align: center;
    color: blue;
    background-color: bisque;
    padding: 15px 32px;
    cursor: pointer;
    margin-top: 20px;
    float: left;
}

p {
    color: blue;
    background-color: bisque;
    width: 200px;
    padding: 10px 10px;
    position: relative;
    margin: auto;
    margin-right: 0px;
    border-radius: 15px;
    margin-top: 10px;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    display: flex;
    justify-content: center;
}

.quoteList {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
}


.logos {
    border: none;
    position: absolute;
    top: 10px;
    left: 0;
    z-index: 1;
    width: 60px;
}


.logo {
    cursor: pointer;
    height: 50px;
    width: 50px;
}
